body{
    padding: 0;
    margin: 0;
}
.row{
    --bs-gutter-x: 0;
}
.Products{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    background-color: rgba(240, 238, 238, 0.541);
    .tagih-bar{       
        @media (min-width:523.1px){
            display: none;            
        }
        @media (max-width:523.1px){
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: fixed;
            padding: 10px 30px;
            background-color: white;
            width: 100%;
            box-shadow: 0 0 7px 1px rgba(158, 158, 158, 0.753);
            z-index: 1000001;
        }
        @media (max-width:310px){
            padding: 10px 20px;
        }
        .navbar-brand{
            @media (max-width:350px){
                margin-right: -10%;
            }            
        }
        h1{
            cursor: pointer;
        }
        img{
            width: 25%;
            height: 25%;
        }
    }
    .vertical{
        z-index: 1000002;
        padding: 20px;
        width: 220px;
        height: 200vh;
        left: 0;
        top: 0;
        position: fixed;
        @media (max-width:779px){
            padding: 10px;
            width: 210px;
        }
        @media (max-width:523px){
            left: -250px;
            padding-top: 40px;
            width: 250px;
            transition: 0.4s;
            -webkit-transition: 0.4s;
            -moz-transition: 0.4s;
            -ms-transition: 0.4s;
            -o-transition: 0.4s;
        }
        @media (max-width:230px){
            left: -240px;
            padding-top: 40px;
            width: 240px;
            transition: 0.4s;
            -webkit-transition: 0.4s;
            -moz-transition: 0.4s;
            -ms-transition: 0.4s;
            -o-transition: 0.4s;
        }
        .tagih-delete{
            cursor: pointer;
            font-weight: 600;
            font-size: 28px;
            z-index: 5;
            position: absolute;
            right: 25px;
            color: grey;
            top: 10px;
            font-family:Arial, Helvetica, sans-serif;
            @media (min-width:523.1px){
                display: none;
            }
        }
        .navbar-brand{
            text-align: center;
            color: black;
            font-weight: 700;
            @media (max-width:523.1px){
                display: none;
            }
        }
        .navbar-nav{
            li{
                a{
                    display: flex;
                    // justify-content: space-between;
                    align-items: center;
                    color: grey;
                    font-weight: 500;
                    &:hover{
                        color: black;
                    }
                    .svg-dash{
                        width: 60px;
                        height: 60px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                    }
                    span{
                        margin-left: 15px;
                    }
                }
            }
        }
    }
