                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    width: 50px;
                    height: 50px;
                    border: solid 1px grey;
                    border-radius: 50%;
                    -webkit-border-radius: 50%;
                    -moz-border-radius: 50%;
                    -ms-border-radius: 50%;
                    -o-border-radius: 50%;
                    @media (max-width:925px){
                        width: 40px;
                        height: 40px;
                    }
                    @media (max-width:767px){
                        width: 50px;
                        height: 50px;
                    }
                    @media (max-width:330px){
                        width: 35px;
                        height: 35px;
                        margin-left: 0;
                        margin-right: 0;
                    }
                    img{
                        @media (max-width:925px){
                            width: 50%;
                            height: 50%;
                        }
                    }
                }
