                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: 110px;
                    @media (max-width:330px){
                        width: 75px;
                    }
                    img{
                        overflow: hidden;
                        width: 50px;
                        height: 50px;
                        object-fit: cover;
                        border: solid 1px grey;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        @media (max-width:925px){
                            width: 40px;
                            height: 40px;
                        }
                        @media (max-width:767px){
                            width: 50px;
                            height: 50px;
                        }
                        @media (max-width:330px){
                            width: 35px;
                            height: 35px;
                        }
                    }
                    h6{
                        @media (max-width:330px){
                            font-size: 13px;
                        }
                        @media (max-width:260px){
                            display: none;
                        }
                    }
                }
            }
        }
